@import "variables.scss";
@import "fonts.scss";

html,
body {
	font-family: "Lato Regular";
	height: 100%;
	margin: 0;
	width: 100%;
}

*:focus {
	outline: none;
}

.main {
	box-sizing: border-box;
	display: flex;
	height: 100%;
	padding: 1em;
	width: 100%;

	div {
		display: flex;
		flex-direction: column;
		width: 100%;

		p {
			color: black;
			font-family: "Lato Light";
			margin: 0;

			&#title {
				font-size: 2em;
				font-weight: bold;
			}

			&#name {
				font-size: 1.4em;
			}
		}

		div {
			padding: 1em;
			box-sizing: border-box;

			img {
				max-width: 100%;
			}
		}
	}
}

#canvas {
	border-radius: 0.4em;
	height: 100%;
	width: 66vw;
}

#fps-counter {
	align-items: center;
	background-color: var(--black);
	border: 2px solid var(--red);
	color: var(--red);
	display: none;
	font-size: 16px;
	height: 3em;
	justify-content: center;
	position: absolute;
	right: 2em;
	top: 2em;
	width: 3em;
}
